import React from "react";
import { graphql } from "gatsby";

import Layout from "../components/Layout";

const IndexPage = ({ data }) => {
  const detail = data.contentfulPortfolio;
  console.log(detail);

  return (
    <Layout>
      <article id="main">
        <header
          style={{
            backgroundImage:
              detail.portfolioAssets[0].file.url !== ""
                ? `linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(${detail.portfolioAssets[0].file.url})`
                : "inherit",
          }}
        >
          <h2>{detail.portfolioTitle}</h2>
        </header>
        <section className="wrapper style5">
          <div className="inner">
            {detail.portfolioUrl && (
              <p>
                <a href={detail.portfolioUrl} target="_blank">
                  View Live
                </a>
              </p>
            )}
            <div>
              <p>{detail.portfolioDescription.portfolioDescription}</p>
            </div>
          </div>
        </section>
      </article>
    </Layout>
  );
};

export default IndexPage;

export const query = graphql`
  query($id: String) {
    contentfulPortfolio(id: { eq: $id }) {
      id
      portfolioAssets {
        file {
          url
          fileName
        }
      }
      portfolioDescription {
        portfolioDescription
      }
      portfolioTitle
      portfolioUrl
      slug
    }
  }
`;
